import Projects from './routes/projects';
import Portfolio from "./routes/portfolio";
import Docs from "./routes/docs";

class App extends Component {
    render() {
        return (
            <div id="app">
                <Header />
                <Router>
                    <Home path="/" />
                    <Projects path="/projects/" />
                    <Portfolio path="/portfolio/" />
                    <Docs path="/docs/" />
                </Router>
            </div>
        )
    }
    
}

export default App;
